import { getMorseCodes, swapMorseCodes } from './charCodes';
import getOptions from './options';
import getAudio from './audio';

export const morseCodes = (options = {}) => getMorseCodes(getOptions(options));

// 编码
export const text2morse = (text: string = '', opts = {}) => {
  const options = getOptions(opts);
  const morseCodes = getMorseCodes(options);
  return text.replace(/\s+/g, options.separator)
    .trim()
    .toLocaleUpperCase()
    .split('')
    .map(char => {
      console.log('-->', char);
      return morseCodes[char] || options.invalid || char;
    })
    .join(options.separator);
};

// 解码
export const morse2text = (morse: string = '', opts = {}) => {
  const options = getOptions(opts);
  const morseCodesSwapped = swapMorseCodes(options);
  return morse.replace(/\s+/g, options.separator)
    .trim()
    .split(options.separator)
    .map(char => {
      return morseCodesSwapped[char] || options.invalid || char;
    })
    .join('');
};

// 播放器
export const morseAudioPlayer = (opts = {}) => {
  // const morseString = typeof value === 'string' ? value : value.morse || text2morse(value.text, opts);
  const options = getOptions(opts);
  return getAudio(options);
};

